function howSlider() {
    var swiper = new Swiper('.how .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 10,
        navigation: {
            nextEl: '.how .swiper-button-next',
            prevEl: '.how .swiper-button-prev',
        },
        pagination: {
            el: '.how .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function feedbackSlider() {
    var swiper = new Swiper('.feedback .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 30,
        observer: true,
        observeParents: true,
        pagination: {
            el: '.feedback .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            768: {
                slidesPerView: 2,
            },
            1100: {
                slidesPerView: 3,
            },
        }
    })
}

function clientsSlider() {
    var swiper = new Swiper('.clients .swiper-container', {
        slidesPerView: 5,
        spaceBetween: 1,
        navigation: {
            nextEl: '.clients .swiper-button-next',
            prevEl: '.clients .swiper-button-prev',
        },
        pagination: {
            el: '.clients .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            576: {
                slidesPerView: 2,
            },
            768: {
                slidesPerView: 3,
            },
            992: {
                slidesPerView: 4,
            },
            1100: {
                slidesPerView: 5,
            },
        }
    })
}

function resultSlider() {
    var swiper = new Swiper('.result .swiper-container', {
        slidesPerView: 2,
        spaceBetween: 30,
        navigation: {
            nextEl: '.result .swiper-button-next',
            prevEl: '.result .swiper-button-prev',
        },
        pagination: {
            el: '.result .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            1100: {
                slidesPerView: 2,
            },
        }
    })
}

$(document).ready(function() {
    $('input[type="tel"]').mask('+7 (999) 999-99-99', { autoclear: false }, { placeholder: '+7 (   )    -  -  ' });

    howSlider()
    feedbackSlider()
    clientsSlider()
    resultSlider()

    $(".faq-card__show").click(function () {
        $(this).parents(".faq-card").toggleClass("_active")
        $(this).siblings(".faq-card__hidden").slideToggle()
    })

    $(".history__regret-show").click(function () {
        $(this).parents(".history__regret").toggleClass("_active")
        $(this).siblings(".history__list").slideToggle()
    })

});
